<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/home-10.svg')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Manual de usuario</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row class="text-center mt-5">
        <v-col>
          <div v-html="body"></div>
        </v-col>
      </v-row>

    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'ManualIndex',
  data: () => ({
    body: '',
  }),
  computed: {},
  mounted() {
    this.getContent()
  },
  methods: {
    getContent() {
      let url = 'settings/3'
      axios.get(url).then((response) => {
        this.body = response.data.value
      })
    },
    goToDetail02() {
      this.$router.push({name: 'Calculator02'})
    },
  }
}
</script>
